/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    @include eltdfBody();
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: auto;
    height: 48px;
    margin: 0;
    padding: 11px 45px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    background-color: $default-background-color;
    transform: translateX(-50%) translateY(-50%) translateZ(0);
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        @include eltdfHeadingStyle('h6');
        letter-spacing: .03em;
    }
    
    del {
        margin: 0 8px 0 0;
        opacity: .5;
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 11px;
    line-height: 1;
    letter-spacing: 5px;
    color: $default-script-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0 4px 0 0;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: baseline;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        font-size: 0;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
            font-size: 11px;
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle($active-style: false) {
    $single-rating-default-icon: "\e031";
    $single-rating-active-icon: "\e033";
    
    @if ($active-style) {
    
        &.active {
    
            &:before {
                content: $single-rating-active-icon;
            }
            
            ~ a {
    
                &:before {
                    content: $single-rating-default-icon;
                }
            }
        }
        
        &:not(.active) {
            
            &:before {
                content: $single-rating-active-icon;
            }
        }
    } @else {
        
        &:before {
            content: $single-rating-default-icon;
            @extend %star-ratings;
        }
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */