/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.eltdf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonSize();
        @include eltdfButtonOutlineColor();
        @include eltdfOutlineButtonAnimation();
        padding: 9px 35px;
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include eltdfButtonOutlineHoverColor();
        }
    }
    
    a.button.added {
        display: none;
    }

    // Overriding button to have the same animation as Default Outline button

    #place_order {
        overflow: hidden;

        &:before,
        &:after {
            content: '';
            width: 1px;
            height: 48px;
            display: inline-block;
            background-color: $first-main-color;
            position: absolute;
            top: 0;
            left: 0;
            transform: rotate(72deg) scale(1, 0);
            transform-origin: top;
            transition: transform .4s cubic-bezier(0.63, 0.25, 0.25, 1);
            will-change: transform;
        }

        &:before {
            left: 44px;
        }

        &:after {
            transform-origin: bottom;
            top: -2px;
            left: 141px;
        }

        &:hover {

            &:before,
            &:after {
                transform: rotate(72deg) scale(1, 1);
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */