/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {

    .eltdf-quantity-buttons {
        margin: 0 19px 0 0;
        position: relative;
        display: inline-block;
        vertical-align: middle;
        border: 1px solid $default-border-color;

        .eltdf-quantity-input,
        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .eltdf-quantity-input {
            position: relative;
            height: 46px;
            width: 48px;
            padding: 0;
            margin: 0 28px 0 0;
            font-family: inherit;
            line-height: 42px;
            border: 0;
            outline: 0;
        }

        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 42px;
            height: 22px;
            line-height: 20px;
            font-size: 16px;
            cursor: pointer;
            transition: color .15s ease-in-out;
            color: $first-main-color;
        }

        .eltdf-quantity-plus {
            top: 5px;
        }

        .eltdf-quantity-minus {
            bottom: 5px;
        }
    }
}

.woocommerce-page .eltdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
            padding: 5px 21px;
            line-height: 37px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */