/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0 59px;

    .touchevents & {
        cursor: pointer;
    }
    
    &:hover {
        
        .eltdf-sc-dropdown {
            max-height: 100vh;
            pointer-events: auto;
            transition: .5s cubic-bezier(0.66, 0.14, 0.58, 1) .1s;
        }
    }

    .eltdf-shopping-cart-inner {
        @include eltdfRelativeHolderLayout();
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;

        .eltdf-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-sc-opener-icon {
                font-size: 18px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }

            .eltdf-sc-opener-count {
                @include eltdfAbsoluteHolderLayout();
                width: auto;
                height: auto;
                top: calc(50% + 5px);
                left: 50%;
                transform: translateX(-50%) translateY(-50%);
                font-size: 10px;
                font-weight: 400;
            }
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    text-align: left;
    box-sizing: border-box;
    max-height: 0;
    pointer-events: none;
    overflow: hidden;
    will-change: transform;
    transition: .2s;
    
    .eltdf-sc-dropdown-inner {
        @include eltdfRelativeHolderLayout();
        width: 381px;
        padding: 34px;
        background-color: $additional-background-color;
        box-sizing: border-box;
    }
    
    .eltdf-sc-dropdown-items {
        @include eltdfRelativeHolderLayout();
    }
    
    .eltdf-sc-dropdown-item {
        position: relative;
        display: flex;
        align-items: flex-start;
        margin-top: 26px;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .eltdf-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 90px;
        margin-right: 21px;
        
        a, img {
            display: block;
        }
    }
    
    .eltdf-sc-dropdown-item-content {
        @include eltdfRelativeHolderLayout();
        padding-right: 20px;
    }
    
    .eltdf-sc-dropdown-item-title {
        @include eltdfDefaultHeadingStyle($margin: false);
        font-size: 15px;
        letter-spacing: .23em;
    }
    
    .eltdf-sc-dropdown-item-price {
        @include eltdfDefaultHeadingStyle($margin: false);
        font-size: 15px;
        letter-spacing: .03em;
    }
    
    .eltdf-sc-dropdown-item-quantity {
        margin: 0;
    }
    
    .eltdf-sc-dropdown-item-remove {
        position: absolute;
        top: 4px;
        right: 0;
        font-size: 20px;
        line-height: 1;
    }
    
    .eltdf-sc-dropdown-subtotal {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 26px;
        
        > * {
            margin: 0;
        }

        .eltdf-sc-dropdown-total-amount {
            @include eltdfDefaultHeadingStyle($margin: false);
            font-size: 15px;
            letter-spacing: .03em;
        }
    }
    
    .eltdf-sc-dropdown-button-holder {
        @include eltdfRelativeHolderLayout();
        margin-top: 28px;
    }
    
    .eltdf-sc-dropdown-button {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonSize(huge);
        @include eltdfButtonOutlineColor();
        @include eltdfOutlineButtonAnimation();
        padding: 9px 26px;
        
        &:hover {
            @include eltdfButtonOutlineHoverColor();
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */