/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltdf-pl-holder {
    @include eltdfRelativeHolderLayout();
	
	&.eltdf-info-on-image {
		
		.eltdf-pli {
			
			.eltdf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .eltdf-pli {
        padding: 0;
        z-index: 100;

	    .eltdf-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .eltdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .eltdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .eltdf-pli-rating-holder {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
            margin: 4px 0 3px;
	    }

	    .eltdf-pli-rating {
		    @include eltdfWooCommerceRatingsStyle();
	    }

        .eltdf-pli-price {
	        @include eltdfWooCommercePriceStyle();
            margin: 6px 0 0;
        }
    }

    .eltdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .eltdf-pli-text {
                opacity: 1;
            }
        }

        .touchevents & {
            cursor: pointer;
        }

        .eltdf-pli-image {
            @include eltdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .eltdf-pli-onsale,
            .eltdf-pli-out-of-stock,
            .eltdf-pli-new-product {
	            @include eltdfWooCommerceMarksGeneralStyle();
            }
        }
            
        .eltdf-pli-text {
            @include eltdfAbsoluteHolderLayout();
            padding: 25px;
            opacity: 0;
            box-sizing: border-box;
            z-index: 101;
            transition: opacity .2s ease-out;
	        
	        @include laptop-landscape-mac {
		        padding: 15px;
	        }
        }

        .eltdf-pli-text-outer {
	        @include eltdfTableLayout();
            background-color: $additional-background-color;
	        text-align: center;
        }

        .eltdf-pli-text-inner {
            @include eltdfTableCellLayout();

            .eltdf-pli-title,
            .eltdf-pli-category a,
            .eltdf-pli-excerpt,
            .eltdf-pli-rating,
            .eltdf-pli-price {
                color: #fff;
            }
	
	        .eltdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .eltdf-pli-add-to-cart {

		        &.eltdf-default-skin {

			        .button,
			        .added_to_cart {
				        @include eltdfButtonOutlineColor();

				        &:hover {
				            @include eltdfButtonOutlineHoverColor();
				        }
			        }
		        }

		        &.eltdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.eltdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include eltdfButtonDefaultStyle();
	            @include eltdfButtonSize();
                padding: 7px 20px;
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }

            // Overriding button to have the same animation as Default Outline button

            .added_to_cart {
                overflow: hidden;

                &:before,
                &:after {
                    content: '';
                    width: 1px;
                    height: 48px;
                    display: inline-block;
                    background-color: $first-main-color;
                    position: absolute;
                    top: 0;
                    left: 0;
                    transform: rotate(72deg) scale(1, 0);
                    transform-origin: top;
                    transition: transform .4s cubic-bezier(0.63, 0.25, 0.25, 1);
                    will-change: transform;
                }

                &:before {
                    left: 44px;
                }

                &:after {
                    transform-origin: bottom;
                    top: -2px;
                    left: 118px;
                }

                &:hover {

                    &:before,
                    &:after {
                        transform: rotate(72deg) scale(1, 1);
                    }
                }
            }
        }

	    .eltdf-pli-link {
		    @include eltdfAbsoluteHolderLayout();
	    }
    }

    .eltdf-pli-text-wrapper {
        @include eltdfRelativeHolderLayout();
        margin: 22px 0 40px;
    }
	
	&.eltdf-product-info-light {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: #fff;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.eltdf-product-info-dark {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: $default-heading-color;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.eltdf-masonry-layout {
		
		.eltdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.eltdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}

.eltdf-page-content-holder.eltdf-grid-col-12 .eltdf-pl-holder.eltdf-three-columns,
.eltdf-full-width .eltdf-page-content-holder.eltdf-grid-col-12 .eltdf-pl-holder.eltdf-four-columns {
	
	.button,
	.added_to_cart {
		@include eltdfButtonSize(medium);
	}
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */