/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;

					.woocommerce-product-gallery__image {

						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;

							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}

					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0;
    }
        
    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 50px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include eltdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }

		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;

			    &:first-child {
				    left: 10px;
				    width: 100%;
				    padding: 0;
			    }

			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 32px;
        float: left;
        box-sizing: border-box;
	
	    .eltdf-single-product-title {
		    display: block;
		    margin: 0 0 10px;
	    }
	
	    .price {
		    @include eltdfWooCommercePriceStyle();

            * {
                @include eltdfHeadingStyle('h5');
            }
	    }
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0;
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdfRelativeHolderLayout();
		    margin: 26px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 51px 0 0;
		
		    > span {
                @include eltdfHeadingStyle('h6');
                display: block;
                margin: 19px 0;
                letter-spacing: .23em;
			
			    a, span {
                    @include eltdfBody();
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .eltdf-woo-social-share-holder {
		    @include eltdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }

            .eltdf-social-title {
                @include eltdfHeadingStyle('h6');
                margin: 0 8px 0 0;
            }

            ul {

                li {
                    margin-right: 20px;
                }
            }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include eltdfRelativeHolderLayout();
		    margin: 45px 0 0;
	    }
	
	    .eltdf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include eltdfRelativeHolderLayout();
		margin: 102px auto 0;
        box-sizing: border-box;
        text-align: center;
		display: block;
		max-width: 900px;
		
		.eltdf-product-tabs-line {
			position: absolute!important;
			left: 0;
			bottom: -1px;
			height: 0;
			border-top: 1px solid $first-main-color;
			transition: width .4s, left .6s cubic-bezier(0.25, 0.1, 0.11, .99);
			
			@include phone-landscape{
				display: none;
			}
		}
		
		ul.tabs {
			@include eltdfRelativeHolderLayout();
            width: auto;
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 1px solid #516268;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
                bottom: -1px;
				display: inline-block;
				vertical-align: middle;
				
				a {
                    @include eltdfHeadingStyle('h6', $margins: false);
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 12px 48px;
				}
			}
		}
		
		.entry-content {
			@include eltdfRelativeHolderLayout();
            padding: 0 20px;
            box-sizing: border-box;
			margin-bottom: 20px;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 25px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 18px 0 20px;
			text-align: left;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 80px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin: 106px 0 0;
				
				.comment-reply-title {
					margin: 0 0 40px;
					@include eltdfHeadingStyle(h4);
					display: block;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					
					> span {
						position: relative;
						display: flex;
						margin: 10px 0;
					}
					
					&.selected {
					
						a {
							@include eltdfWooCommerceSingleProductRatingsStyle(true);
						}
					}
					
					a {
						position: relative;
						display: block;
						color: transparent;
						margin: 0;
						font-size: 0;
						line-height: 1;
						@include eltdfWooCommerceSingleProductRatingsStyle();
					}
				}

                .form-submit {

                    .submit {
	                    margin-top: 22px;
	                    padding: 10px 71px;

                        &:hover {
                            background-color: $first-main-color;
                        }
                    }
                }
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 109px 0 0;
		
		> h2 {
            @include eltdfHeadingStyle('h4');
            margin: 25px 0 34px 0;
		}
		
		.product {

            .eltdf-pl-image {

                .eltdf-pl-text {
                    padding: 31px;
                }
            }
			
			.eltdf-pl-text-wrapper {
                margin: 25px 0 0;
                text-align: center;

                .eltdf-pl-rating-holder {
                    margin: 3px 0;
                }

                .price {
                    margin: 6px 0 0;

                    del {
                        margin: 0 19px 0 0;
                    }
                }
			}
			
			.button,
			.added_to_cart {
				padding: 7px 20px;
			}

            // Overriding button to have the same animation as Default Outline button

            .added_to_cart {
                overflow: hidden;

                &:before,
                &:after {
                    content: '';
                    width: 1px;
                    height: 44px;
                    display: inline-block;
                    background-color: $first-main-color;
                    position: absolute;
                    top: 0;
                    left: 0;
                    transform: rotate(72deg) scale(1, 0);
                    transform-origin: top;
                    transition: transform .4s cubic-bezier(0.63, 0.25, 0.25, 1);
                    will-change: transform;
                }

                &:before {
                    left: 40px;
                }

                &:after {
                    transform-origin: bottom;
                    top: -2px;
                    left: 92px;
                }

                &:hover {

                    &:before,
                    &:after {
                        transform: rotate(72deg) scale(1, 1);
                    }
                }
            }
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */