/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.eltdf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 27px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: #{$sselect-input-height}px;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
            text-align: initial;
            border: 1px solid $first-main-color;

            .select2-selection--single {
                height: 45px;
                padding: 7px 19px;
                background-color: transparent;

                .select2-selection__rendered {
                    @include eltdfBody();
                    font-size: 17px;
                }

                .select2-selection__arrow {
                    font-size: 18px;

                    &:before {
                        content: '\37';
                    }
                }
            }
        }
    }
	
	.select2-container--default .select2-dropdown {
		border: none;
	}
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */