/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.eltdf-plc-holder {
    @include eltdfRelativeHolderLayout();

    .eltdf-plc-outer {
        @include eltdfRelativeHolderLayout();
    }

    .eltdf-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        &:hover {

            .eltdf-plc-image-outer {

                .eltdf-plc-btn-overlay-outer {
                    -webkit-clip-path: inset(0 0 0 0);
                    clip-path: inset(0 0 0 0);
                    transition: .4s cubic-bezier(0.48, 0.57, 0.33, 0.89);

                    .eltdf-plc-add-to-cart {
                        animation: .4s eltdfFadeInUp forwards .2s;
                    }
                }
            }
        }

        .touchevents & {
            cursor: pointer;
        }

        .eltdf-plc-image-outer {
            @include eltdfRelativeHolderLayout();
            margin: 0 0 25px;

            .eltdf-plc-image {
                @include eltdfRelativeHolderLayout();

                .eltdf-plc-onsale,
                .eltdf-plc-out-of-stock,
                .eltdf-plc-new-product {
	                @include eltdfWooCommerceMarksGeneralStyle();
                }
            }

            .eltdf-plc-btn-overlay-outer {
                @include eltdfAbsoluteHolderLayout();
                padding: 25px;
                box-sizing: border-box;
                z-index: 100;
                -webkit-clip-path: inset(0 0 100% 0);
                clip-path: inset(0 0 100% 0);
                transition: .2s cubic-bezier(0.48, 0.57, 0.33, 0.89);

                .eltdf-plc-btn-overlay-inner {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: $additional-background-color;

                    .eltdf-plc-add-to-cart {
                        margin: 0;
                        opacity: 0;
                        transform: translate3d(0, 30%, 0);
                    }
                }
            }
        }

        .eltdf-plc-text {
            text-align: center;
        }

        .eltdf-plc-title {
            margin: 0 0 4px;
        }
	
	    .eltdf-plc-category {
		    margin: 11px 0 3px;
	    }

        .eltdf-plc-excerpt {
            color: $default-text-color;
            line-height: 1.3em;
            margin: 11px 0 3px;
        }

        .eltdf-plc-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 4px 0 3px;
        }

        .eltdf-plc-rating {
	        @include eltdfWooCommerceRatingsStyle();
        }

        .eltdf-plc-price {
	        @include eltdfWooCommercePriceStyle();
            margin: 6px 0 0;
        }
	
	    .button,
	    .added_to_cart {
		    @include eltdfButtonOutlineColor();
		    @include eltdfButtonSize();
            padding: 7px 20px;
		    white-space: nowrap;
		    z-index: 3;
	    }
	    
	    .added_to_cart {
		    @include eltdfButtonDefaultStyle();
	    }
	
	    .button.added {
		    display: none;
	    }

        // Overriding button to have the same animation as Default Outline button

        .added_to_cart {
            overflow: hidden;

            &:before,
            &:after {
                content: '';
                width: 1px;
                height: 48px;
                display: inline-block;
                background-color: $first-main-color;
                position: absolute;
                top: 0;
                left: 0;
                transform: rotate(72deg) scale(1, 0);
                transform-origin: top;
                transition: transform .4s cubic-bezier(0.63, 0.25, 0.25, 1);
                will-change: transform;
            }

            &:before {
                left: 44px;
            }

            &:after {
                transform-origin: bottom;
                top: -6px;
                left: 88px;
            }

            &:hover {

                &:before,
                &:after {
                    transform: rotate(72deg) scale(1, 1);
                }
            }
        }
	
	    .eltdf-plc-add-to-cart {
		
		    &.eltdf-default-skin {
			
			    .button,
			    .added_to_cart {
				    @include eltdfButtonOutlineColor();
				
				    &:hover {
					    @include eltdfButtonOutlineHoverColor();
				    }
			    }
		    }
		
		    &.eltdf-light-skin {
			
			    .button,
			    .added_to_cart {
				    color: $default-heading-color;
				    background-color: #fff;
				
				    &:hover {
					    color: #fff;
					    background-color: $first-main-color;
				    }
			    }
		    }
		
		    &.eltdf-dark-skin {
			
			    .button,
			    .added_to_cart {
				    color: #fff;
				    background-color: $default-heading-color;
				
				    &:hover {
					    color: #fff;
					    background-color: $first-main-color;
				    }
			    }
		    }
	    }

        .eltdf-plc-link {
            @include eltdfAbsoluteHolderLayout();
        }
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            top: calc(50% - 94px);
        }

        .owl-prev {
            left: -75px;
        }

        .owl-next {
            right: -75px;
        }
    }

    .owl-dots {
        margin: 80px 0 0;
    }

    &.eltdf-simple-layout {
        
        .eltdf-plc-image-outer {
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 15px 0 0;
        }

        .eltdf-plc-text-wrapper {
            position: relative;
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 0 0 15px;
        }

        .eltdf-plc-text,
        .eltdf-plc-text-outer,
        .eltdf-plc-text-inner {
            @include eltdfRelativeHolderLayout();
            -webkit-transform: translateZ(0);
        }
    }
	
	&.eltdf-plc-nav-light-skin {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				color: $header-light-color;
				
				&:hover {
					color: $header-light-hover-color;
				}
			}
		}
	}
	
	&.eltdf-plc-pag-inside-slider {
		
		.owl-prev,
		.owl-next {
			transform: translateY(-50%);
		}
		
		.owl-dots {
			position: absolute;
			left: 0;
			bottom: 10px;
			margin: 0;
		}
	}
	
	&.eltdf-plc-pag-light-skin {
		
		.owl-dots {
			
			.owl-dot {

				span {

                    &:before {
                        color: #fff
                    }
				}

                &:after {
                    border-color: #fff;
                }
			}
		}
	}
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */