/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 90px;
    $inner-padding: 24px; // distance between image and content
    $widget-list-item-padding: 24px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include eltdfHeadingStyle($margins: false);
        margin: 0;
        font-size: 15px;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {

        .star-rating {
            margin-bottom: 9px;
            margin-top: 5px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {

        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {

        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 20px;
            margin-bottom: 8px;
        }

        .price_slider_wrapper {

            .ui-widget-content {
                background-color: #516268;
                height: 1px;
                position: relative;

                .ui-slider-handle {
                    background: transparent;
                    cursor: pointer;
                    height: 11px;
                    width: 11px;
                    margin: 0 0 0 -6px;
                    outline: medium none;
                    position: absolute;
                    top: -5px;
                    width: 11px;
                    z-index: 2;
                    
                    &:after {
	                    content: '';
                        display: block;
	                    margin: 0 auto;
                        background-color: $first-main-color;
	                    width: 100%;
	                    height: 100%;
	                    transform: scaleX(.1);
	                    transition: transform .25s cubic-bezier(.62,.07,0,.99);
                    }
	                
	                &:hover {
		                &:after {
			                transform: scaleX(1);
		                }
	                }

                    &:last-child {
                        transform: translateX(0);
                    }
                }

                .ui-slider-range {
                    background-color: $first-main-color;
                    display: block;
                    height: 1px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 32px 0 0;

            .button {
                @include eltdfButtonOutlineColor();
                float: left;
                padding: 6px 45px;

                &:hover {
                    @include eltdfButtonOutlineHoverColor();
                }
            }

            .price_label {
                float: right;
                font-size: 16px;
                line-height: 44px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {

        ul {
            margin: -10px 0 35px;

            li {
                margin: 0 0 21px;
                min-height: $thumb;
                position: relative;
                list-style: none;
                padding: 0;
                clear: both;

                a {

                    img {
                        left: 0;
                        max-width: $thumb !important;
                        position: absolute;
                        top: $widget-list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 28px 0 0 #{$thumb + $inner-padding};
                        position: relative;
	                    letter-spacing: 0.23em;
                    }

                    &:after {
                        content: '';
                        position: absolute;
                        top: $widget-list-item-padding;
                        left: 0;
                        display: block;
                        width: $thumb;
                        height: $thumb;
                        background-image: url('../../assets/img/icon-overlay.jpg');
                        opacity: 0;
                        transition: opacity .2s ease-in-out;
                    }

                    &:hover {

                        &:after {
                            opacity: 1;
                        }
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};

                    &.woocommerce-Price-amount {
                        @include eltdfHeadingStyle($margins: false);
                        font-size: 15px;
                    }
                }

                ins {
                    @include eltdfHeadingStyle($margins: false);
                    font-size: 15px;
                }

                del {
                    display: none;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart {

        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include eltdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {

        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {

            @include eltdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {

            .button {
                @include eltdfButtonDefaultStyle();
                @include eltdfButtonSize();
                @include eltdfButtonSolidColor();

                &:hover {
                    @include eltdfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */